import { useState } from "react";
import QLoader from "../../../../../../Loader/QLoader";
import Overlay from "../../../../../../Search/Overlay";
import SearchModalBackdrop from "../../../../../../Search/SearchModalBackdrop";
import { useAcademicChapterRemoveSubject } from "../../../../../../hooks/member_tab/subject-api";
import style from "../../../ClassTeacher/Profile/EditConfirm.module.css";

const AcademicCardMenu = ({ onClose, cid, onRefetch }) => {
  const [disabled, setDisabled] = useState(false);
  const [academicChapterRemoveSubject] = useAcademicChapterRemoveSubject();

  const onDelete = () => {
    if (cid) setDisabled((pre) => !pre);
    academicChapterRemoveSubject({
      cid: cid,
    })
      .then(() => {
        onRefetch();
        setDisabled((pre) => !pre);
        onClose();
      })
      .catch({});
  };

  return (
    <>
      <SearchModalBackdrop onClose={onClose} />
      <Overlay
        customStyle={{
          zIndex: "1200",
        }}
      >
        <div className={style.edit_modal_container} style={{ width: "200px" }}>
          <h6 className={style.remove} onClick={onDelete}>
            Delete
          </h6>
        </div>
        {disabled && <QLoader />}
      </Overlay>
    </>
  );
};

export default AcademicCardMenu;
